<template>
    <section class="section rsvp-area center-text">
        <div class="container">
            <div class="row">
                <div class="col-sm-12">
                    <div class="heading">
                        <h2 class="title">
                            Confirma tu asistencia
                        </h2>
                        <span class="heading-bottom">
                            <img class="suculenta-icon" src="../../src/assets/images/suculentaIcon.png">
                        </span>
                    </div>
                    <!-- Botones de confirmacion -->
                    <div class="row">
                        <div class=" col-md-12 offset-sm-12 ">
                            <div class="confirm-button">
                                <button class="btn btn-primary" type="button" v-on:click="setYes">
                                    ¡Ahí estaré! 🙂
                                </button>
                            </div>
                        </div>
                        <div class=" col-md-12  offset-sm-12 ">
                            <div class="confirm-button">
                                <button class="btn btn-primary" type="button" v-on:click="setNo">
                                    No podré asistir ☹️
                                </button>
                            </div>
                        </div>
                    </div>
                    <!-- form -->
                    <form v-if="flag === 1">
                        <p class="input-space">
                            Usted cuenta con {{guest.plusone}} asientos.
                        </p>
                        <div class="form-group">
                            <div class="row">
                                <div class="col-md-4 offset-md-4 col-sm-4 offset-sm-4 input-space" v-for="item in items" :key="item.message">
                                    <input v-model="guests[item]" class="form-control" id="plusones" placeholder="Ingrese invitado">
                                </div>
                            </div>
                        </div>
                        <button class="border-btn" type="button" v-on:click="send">
                                    Enviar
                                </button>
                      <!--   <button class="border-btn" v-on:click="flag = 1">
                            Enviar
                        </button> -->
                    </form>
                    <!-- image-gallery -->
                </div>
                <!-- col-sm-10 -->
            </div>
            <!-- row -->
        </div>
        <!-- container -->
    </section>
</template>
<script>
import axios from 'axios'
var plusone = 0
var flag = 0

export default {
  name: 'rsvp',
  data () {
    return {
      flag: flag,
      plusone: plusone,
      message: '',
      guests: []
    }
  },
  props: ['guest', 'items'],
  methods: {
    setYes: function (event) {
      this.flag = 1
    },
    setNo: function (event) {
      this.flag = 2
      axios
        .put('https://phoenixdawn.herokuapp.com/api/guest/denyResponse', {
          id: this.guest.id
        }).then(response => {
          console.log('Nay')
        })
    },
    send: function (event) {
      axios
        .put('https://phoenixdawn.herokuapp.com/api/guest/updateGuestList', {
          response: true,
          plusonelist: this.guests,
          id: this.guest.id
        }).then(response => {
          console.log('YAY')
        })
    }
  }
}
</script>



